import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";
import { GoSearch } from "react-icons/go";
import { useGlobalContext } from "../../context";
import useWindowDimensions from "../../hooks/useWindowDimensions";

const Navbar = () => {
    const {
        setSearchResults,
        setExpandBiography,
        setModalContent,
        isScroll,
        setIsScroll,
    } = useGlobalContext();

    const [windowDimensions] = useWindowDimensions();

    const navRef = useRef();

    const [isHover, setIsHover] = useState(false);
    const toggleHover = () => setIsHover(!isHover);

    const changeHeight = () => {
        if (windowDimensions.width < 950) {
            if (isHover) {
                navRef.current.style.height = "3rem";
            } else {
                navRef.current.style.height = "1rem";
            }
        }
        return;
    };

    return (
        <nav
            ref={navRef}
            className={isScroll ? "navbar hidden-navbar" : "navbar"}
            id={isHover ? "visible-navbar" : ""}
            onMouseEnter={toggleHover}
            onMouseLeave={toggleHover}
            onTouchStart={toggleHover}
        >
            <ul id="navbar-list">
                <li>
                    <Link
                        className={
                            isScroll ? "nav-link hidden-link" : "nav-link"
                        }
                        id={isHover ? "visible-link" : ""}
                        to="/"
                        onClick={() => {
                            setSearchResults({
                                category: "movie",
                                movie: [],
                                tv: [],
                                people: [],
                            });
                            setExpandBiography(false);
                            setModalContent("");
                            setIsScroll(false);
                        }}
                    >
                        Home
                    </Link>
                </li>
                <li>
                    <Link
                        className={
                            isScroll ? "nav-link hidden-link" : "nav-link"
                        }
                        id={isHover ? "visible-link" : ""}
                        to="/watchlist"
                        onClick={() => {
                            setExpandBiography(false);
                            setModalContent("");
                            setIsScroll(false);
                        }}
                    >
                        Watchlist
                    </Link>
                </li>
                <li>
                    <Link
                        className={
                            isScroll
                                ? "nav-link search-icon-link hidden-link"
                                : "nav-link search-icon-link"
                        }
                        id={isHover ? "visible-link" : ""}
                        to="/search"
                        onClick={() => {
                            setExpandBiography(false);
                            setModalContent("");
                            setIsScroll(false);
                        }}
                    >
                        <GoSearch className="link-icon" />
                    </Link>
                </li>
            </ul>
        </nav>
    );
};

export default Navbar;
